.rightbar{
    flex: 3.5;
}

.rightbarWrapper{
    padding: 20px 10px 0 0;
}

.brithdayContainer{
    display: flex;
    align-items: center;
}

.birthdayImg{
    margin-left: 30px;
    width: 40px;
    height: 40px;
    margin-right: 10px;
}

.birthdayText{
    font-weight: 300;
    font-size: 15px;
}

.rightbarAd{
    width: 300px;
    border-radius: 10px;
    margin: 30px;
}

.rightbarTitle{
    margin-left: 28px;
    margin-bottom: 20px;
}

.rightbarFriendList{
    padding: 0;
    margin: 0;
    list-style: none;
}

.rightbarTitle{
    margin-right: 10px;    
    font-size: 18px;
    font-weight: 500;
    margin-bottom: 10px;
}

.rightbarInfo{
    margin-bottom: 30px;
    margin-left: 30px;
}

.rightbarInfoItem{
    margin-bottom: 10px;
    margin-left: 10px;
}

.rightbarInfoKey{
    font-weight: 500;
    margin-right: 15px;
    color: #555;
}

.rightbarInfoValue{
    font-weight: 300;
}

.rightbarFollowings{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.rightbarFollowing{
    margin-left: 30px;
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    cursor: pointer;
}

.rightbarFollowImg{
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius: 5px;
}